/* src/App.css */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1e1e1e; /* Dark background for the body */
  color: #e0e0e0; /* Light text color for readability */
}

.header {
  background: linear-gradient(90deg, #333, #555); /* Dark gradient */
  color: #e0e0e0; /* Light text color */
  padding: 20px;
  text-align: center;
  border-bottom: 4px solid #444;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
}

.header h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: 700;
}

main {
  padding: 20px;
  max-width: 900px;
  margin: 20px auto;
}

.form {
  background: #2c2c2c; /* Dark form background */
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  transition: box-shadow 0.3s ease;
}

.form:hover {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-size: 1.1rem;
  margin-bottom: 8px;
  color: #c0c0c0; /* Lighter text for labels */
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #444;
  border-radius: 8px;
  box-sizing: border-box;
  font-size: 1rem;
  background-color: #333; /* Dark input background */
  color: #e0e0e0; /* Light text color */
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #666;
  box-shadow: 0 0 8px rgba(100, 100, 100, 0.5);
  outline: none;
}

.submit-button {
  background: linear-gradient(90deg, #4a90e2, #007bff); /* Gradient button */
  color: #fff;
  border: none;
  padding: 12px 24px;
  font-size: 1.1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
}

.submit-button:hover {
  background: linear-gradient(90deg, #007bff, #0056b3);
  transform: scale(1.02);
}

footer {
  text-align: center;
  padding: 15px;
  background-color: #111; /* Dark footer background */
  color: #888; /* Light footer text color */
  position: fixed;
  bottom: 0;
  width: 100%;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.4);
}

footer p {
  margin: 0;
  font-size: 0.9rem;
}
